import { buziAPI } from '@/api';
export default {
	data() {
		return {
			doctorList:[]
		};
	},
	components: {},
	computed: {},
	onPageScroll(e) {},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReachBottom() {
	},
	onLoad() {
		this.getDoctorList()
	},
	methods: {
		getDoctorList(){
			let that = this
			buziAPI.getDoctorList(
				res => {
					that.doctorList = res.datas[0].datas;
					
				}
			);
		}
	}
};
